  transition: all .5s;\n}\n\n.card__info-title{\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.card__info{\n  padding: 20px 20px;\n}\n.card__info-name{\n  font-size: 0.8rem;\n  font-weight: 600;\n}\n.card__info-city{\n  margin-top: 5px;\n  font-weight: 300;\n  font-size: 0.8rem;\n}\n.card__info-description {\n  margin-top: 20px;\n  font-size: 0.8rem;\n  font-weight: 200;\n  opacity: 0.8;\n}\n\n.card_detail-restaurant{\n  margin-top: 20px;\n  padding: 10px 10px;\n  background-color: aliceblue;\n  text-align: center;\n  border-radius: 8px;\n}\n.card_detail-restaurant a{\n  color: #1D3557;\n  padding: 14px 14px;\n  font-weight: 600;\n}\n\n.truncate {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 3;\n  -webkit-box-orient: vertical;\n}\n\n/*\n* Detail-Page\n*\n*/\n\n\n.detail-restaurant {\n  display: flex;\n  flex-wrap: wrap;\n}\n\n.restaurant-rating {\n  color: black;\n  font-weight: bold;\n  margin-top: 20px;\n  padding: 0;\n}\n\n.detail-restaurant .detail-main__block {\n  border: 1px solid #1D3557;\n}\n\n.detail-restaurant .detail-main img {\n  width: 100%;\n  height: 400px;\n  object-fit: cover;\n}\n\n.detail-restaurant .detail-main__container{\n  padding: 1rem;\n}\n\n.detail-restaurant .detail-main__description p{\n  padding-top: 10px;\n  opacity: 0.8;\n}\n\n.detail-restaurant .detail-main__description h3{\n  color: #1D3557;\n}\n\n.detail-restaurant .detail-main-menu{\n  margin-top: 20px;\n}\n\n.detail-restaurant .detail-main-menu h3{\n  color: #1D3557;\n}\n\n.detail-restaurant .detail-main-menu__container{\n  margin-top: 20px;\n  display: flex;\n  flex-direction: column;\n}\n\n.detail-restaurant .detail-main-menu__container .detail-main__menu{\n  margin-bottom: 10px;\n}\n\n.detail-restaurant .detail-main-menu__container .detail-main__menu ul li{\n  list-style: none;\n  opacity: 0.8;\n}\n\n.detail-restaurant .detail-main-menu__container .detail-main__menu p{\n  font-weight: 600;\n  margin-bottom: 10px;\n}\n\n.detail-restaurant .detail-main-menu__container .detail-main__drinks{\n  margin-bottom: 10px;\n}\n\n.detail-restaurant .detail-main-menu__container .detail-main__drinks ul li{\n  list-style: none;\n  opacity: 0.8;\n}\n\n.detail-restaurant .detail-main-menu__container .detail-main__drinks p{\n  font-weight: 600;\n  margin-bottom: 10px;\n}\n\n.detail-restaurant .main-title {\n  padding: 1rem;\n  display: flex;\n  justify-content: space-between;\n}\n\n.detail-restaurant .main-title h2 {\n  margin-top: 20px;\n  margin-bottom: 10px;\n}\n\n.detail-restaurant .main-title__title {\n  font-size: 18px;\n  font-weight: bold;\n}\n\n.detail-restaurant .detail-sidebar {\n  padding: 1.5rem;\n  width: 100%;\n  min-width: 200px;\n}\n\n.detail-restaurant .main-navigation {\n  display: flex;\n  justify-content: space-between;\n  border: 1px solid #1D3557;\n  margin: 10px 0;\n  padding: 1rem;\n}\n\n.detail-restaurant .description-container {\n  padding: 1rem;\n  border: 1px solid #1D3557;\n}\n\n.detail-restaurant .description-container h3 {\n  margin-top: 0;\n}\n\n.detail-restaurant .description-container .main-content__menu-container {\n  display: flex;\n  flex-wrap: wrap;\n}\n\n.detail-restaurant .description-container .main-content__menu-container > div {\n  padding-right: 2rem;\n} \n\n.detail-main__review{\n  padding: 1rem;\n}\n\n.detail-main__review .review__review-title{\n  color: #1D3557;\n}\n\n.review__review-container{\n  margin-top: 20px;\n  opacity: 0.8;\n}\n\n.review__review-container .review-item{\n  margin-bottom: 20px;\n}\n\n.review__review-container .review-item .review-item__name{\n  font-weight: 600;\n}\n\n.review__review-container .review-item .review-item__review{\n  font-size: 0.8rem;\n  opacity: 0.8;\n  padding-top: 10px;\n}\n\n/*\n  Like\n*/\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #367cdc;\n  \n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  \n  font-size: px;\n  color: white;\n  cursor: pointer;\n}\n\n/*\n* Favourite\n*/\n\n.content-favourite{\n  margin-top: 120px;\n}\n\n/* \n* Footer \n*/\n\nfooter {\n  margin-top: 20px;\n  padding: 30px 0;\n  background-color: #1D3557;\n  font-size: 0.6rem;\n  text-align: center;\n  color: #fff;\n}\n\n"],"sourceRoot":""}]);
